.friends_main {
    background-color: #bcbceb;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 25px;
    width: 370px;
    box-shadow: 0 0 10px #3d2f26ed;
    border-radius: 10px;
}

.friends_item {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 70%;
    border-bottom: #7e6f7498 2px;
    box-shadow: 0 0 8px #1a044eed;
    padding: 20px;
    background-color: #a9b98e49;
}

.friends_status {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 20px;
}

.friends_on {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 20px;
    background-color: rgb(4, 73, 4);
}

.friends_off {
    /* composes:friends_on; */
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 20px;
    background-color: rgb(94, 6, 6);
}

.friends_avatar {
    margin-left: 20px;
    width: 80px;
    margin-right: 10px;
}

.friends_name {
    align-content: center;
    font-weight: 600;
    font-size: 30px;
    margin-bottom: 15px;
    color: #1a1930;
}

.friends__list {
    align-items: center;
    display: flex;
    flex-direction: column;
    width: inherit;
    padding: 0;
}