.statistics_main {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 10px;
    box-shadow: 0 0 10px #e7f0f0ed;
    margin-top: 30px;
}

.statistics_title {
    font-weight: 700;
    font-size: 20px;
    margin-bottom: 15px;
    color: #888486;
}

.statistics_list {
    width: 370px;
    display: flex;
    justify-content: space-evenly;
    padding: 0;
}

.statistics_label {
    font-weight: 500;
    font-size: 20px;
    color: #47222f;
    margin-bottom: 5px;
}

.statistics_item {
    display: flex;
    flex-direction: column;
    padding: 10px;
    width: inherit;
    align-items: center;
}

.statistics_percentage {
    font-weight: 700;
    font-size: 20px;
    color: #524a4d;
}
